.toggleBtn{
  position: absolute;
  top: 20px;
  left: 20px;
  width: 30px;
  height: 30px;
  outline: none;
  text-align: center;
  padding: 0;
  background-color: black;
  border-radius: 50%;
  color: white;
  z-index: 2;
}

.toggleBtn:hover{
  background-color: grey;
}


