.loginInfo{
  width: 70%;
  margin: 5px auto;
  display: -ms-flexbox;
  display: flex;
}

.loginInfo button{
  height: 45px;
  background: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.active{
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 1.5rem;
  color: #1CB94E;
  -webkit-transition: font-size .35s linear;
  -o-transition: font-size .35s linear;
  transition: font-size .35s linear;
}

.other{
  -ms-flex-positive: 1;
      flex-grow: 1;
  font-size: 0.9rem;
  -webkit-transition: font-size .35s linear;
  -o-transition: font-size .35s linear;
  transition: font-size .35s linear;
}


